import { z } from 'zod'
import { objectPick } from '@antfu/utils'

import type { BookingStatus, TourBooking } from '@/shared/types'
import type { RequireAtLeastOne } from '@/shared/lib/utils'

import { type ApiEndpointsAndSchemas, client } from '../lib'

import { TourBookingSchema } from '../schemas'

const prefix = '/v1/tour-bookings'

export const endpoints = {
  update: {
    url: (
      { tourBookingId }: Pick<UpdateTourBookingParams, 'tourBookingId'>,
    ) => `${prefix}/${tourBookingId}`,
    method: 'put',
    schema: z.object({
      data: TourBookingSchema.pick({
        scheduledDepartureId: true,
        status: true,
        arrivedPassengerCount: true,
      }),
    }),
  },

} satisfies ApiEndpointsAndSchemas

export { endpoints as scheduledExperienceEndpoints }

export type UpdateTourBookingParams = {
  tourBookingId: TourBooking['id']
} & RequireAtLeastOne<{
  arrivedPassengerCount: number
  status: BookingStatus
}>
export async function updateTourBooking(
  { tourBookingId, arrivedPassengerCount, status }: UpdateTourBookingParams,
) {
  const { url, method, schema } = endpoints.update

  const { data } = await client[method](
    url({ tourBookingId }),
    {
      arrivedPassengerCount,
      status,
    },
    schema,
  )

  return objectPick(
    data, ['scheduledDepartureId', 'status', 'arrivedPassengerCount'],
  )
}
