import { queryOptions, useMutation, useQuery, useQueryClient } from '@tanstack/vue-query'
import { produce, setAutoFreeze } from 'immer'

import type { ScheduledDeparture } from '@/shared/types'

import {
  type GetDepartureByIdKeyParams,
  type GetDeparturesByOperationalBaseAndDateKeyParams,
  getDepartureById,
  getDepartureSuggestedResources,
  getDeparturesByOperationalBaseAndDate,
  updateScheduledDeparture,
  updateScheduledDepartureUsers,
} from './api'
import { paramsAnd, trimKey } from '../lib'

setAutoFreeze(false)

const entity = 'scheduled-departure'
const Scopes = {
  ByOperationalBaseAndDate: 'get-by-operational-base-and-date',
  ById: 'get-by-id',
  SuggestedResources: 'get-suggested-resources',
}

const keys = {
  byOperationalBaseAndDate: (params: GetDeparturesByOperationalBaseAndDateKeyParams) => [
    { entity, scope: Scopes.ByOperationalBaseAndDate, ...params },
  ] as const,
  byId: (params: GetDepartureByIdKeyParams) => [
    { entity, scope: Scopes.ById, ...params },
  ] as const,
  suggestedResources: (params: GetDepartureByIdKeyParams) => [
    { entity, scope: Scopes.SuggestedResources, ...params },
  ] as const,
}

export {
  keys as scheduledDepartureKeys,
  entity as scheduledDepartureEntities,
  Scopes as scheduledDepartureScopes,
}

export function useDeparturesByOperationalBaseAndDateQuery(
  params: GetDeparturesByOperationalBaseAndDateKeyParams,
) {
  return queryOptions({
    queryKey: keys.byOperationalBaseAndDate(params),
    queryFn: ({ queryKey }) => getDeparturesByOperationalBaseAndDate(trimKey(queryKey)),
    enabled: paramsAnd(params),
    refetchOnWindowFocus: false,
  })
}

export function useDeparturesByOperationalBaseAndDate(
  params: GetDeparturesByOperationalBaseAndDateKeyParams,
) {
  return useQuery(useDeparturesByOperationalBaseAndDateQuery(params))
}

export function useDepartureQuery(params: GetDepartureByIdKeyParams) {
  return queryOptions({
    queryKey: keys.byId(params),
    queryFn: ({ queryKey }) => getDepartureById(trimKey(queryKey)),
    enabled: paramsAnd(params),
  })
}

export function useDeparture(params: GetDepartureByIdKeyParams) {
  return useQuery(useDepartureQuery(params))
}

export function useScheduledDepartureUpdate() {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: updateScheduledDeparture,
    onSuccess({ comment, status }, { scheduledDepartureId }) {
      queryClient.setQueryData(
        useDepartureQuery({ scheduledDepartureId }).queryKey,
        (data) => data ? ({ ...data, comment, status }) : undefined,
      )

      queryClient.setQueriesData<ScheduledDeparture[]>(
        {
          queryKey: [{ entity, scope: Scopes.ByOperationalBaseAndDate }],
          type: 'active',
        },
        (data) => produce(data ?? [], (draft) => {
          for (const departure of draft) {
            if (departure.id === scheduledDepartureId) {
              departure.comment = comment
              departure.status = status
              return
            }
          }
        }),
      )
    },
  })
}

export function useDepartureSuggestedResourcesQuery(params: GetDepartureByIdKeyParams) {
  return queryOptions({
    queryKey: keys.byId(params),
    queryFn: ({ queryKey }) => getDepartureSuggestedResources(trimKey(queryKey)),
    enabled: paramsAnd(params),
  })
}

export function useDepartureSuggestedResources(params: GetDepartureByIdKeyParams) {
  return useQuery(useDepartureSuggestedResourcesQuery(params))
}

export function useScheduledDepartureUsersUpdate() {
  const queryClient = useQueryClient()

  return useMutation({
    mutationFn: updateScheduledDepartureUsers,
    onSuccess(scheduledUsers, { scheduledDepartureId }) {
      queryClient.setQueriesData<ScheduledDeparture[]>(
        {
          queryKey: [{ entity, scope: Scopes.ByOperationalBaseAndDate }],
          type: 'active',
        },
        (data) => produce(data ?? [], (draft) => {
          for (const departure of draft) {
            if (departure.id === scheduledDepartureId) {
              departure.scheduledUsers = [...scheduledUsers]
              return
            }
          }
        }),
      )
    },
  })
}
