import type { z } from 'zod'
import { objectPick } from '@antfu/utils'

import type { Booking, Passenger, PassengerBooking, TourBooking } from '@/shared/types'
import type { BookingSchema, MainBookingPassengerSchema, TourBookingSchema } from '../schemas'

export function normalizeMainBookingPassenger(
  mainBookingPassenger: z.infer<typeof MainBookingPassengerSchema>,
): Passenger | null {
  if (mainBookingPassenger === null) return mainBookingPassenger

  return objectPick(mainBookingPassenger, ['id', 'name', 'email', 'phone'])
}

export function normalizeBooking(
  booking: z.infer<typeof BookingSchema>,
): Booking {
  const { mainPassengerBooking } = booking

  return {
    ...objectPick(
      booking,
      ['id', 'code', 'externalReference'],
    ),
    mainPassenger: normalizeMainBookingPassenger(mainPassengerBooking),
  }
}

export function normalizeTourBooking(
  tourBooking: z.infer<typeof TourBookingSchema>,
): TourBooking {
  const { date, booking, url, passengerBookings } = tourBooking

  return {
    ...objectPick(
      tourBooking,
      [
        'id',
        'externalId',
        'code',
        'status',
        'totalPassengerCount',
        'arrivedPassengerCount',
        'waiverStatus',
        'pickupPlace',
      ],
    ),
    date: new Date(date),
    booking: normalizeBooking(booking),
    passengerBookings: passengerBookings as PassengerBooking[],
    link: url,
  }
}
