import type { z } from 'zod'
import { objectPick } from '@antfu/utils'

import type { Tour } from '@/shared/types'
import type { TourSchema } from '../schemas'

export function normalizeTour(
  departure: z.infer<typeof TourSchema>,
): Tour {
  return objectPick(departure, ['id', 'code', 'name', 'thumbnailUrl'])
}
