import type { z } from 'zod'
import { objectPick } from '@antfu/utils'

import type { Departure } from '@/shared/types'
import type { DepartureSchema } from '../schemas'

export function normalizeDeparture(
  departure: z.infer<typeof DepartureSchema>,
): Departure {
  return objectPick(departure, ['id', 'time', 'durationSeconds'])
}
