import type { z } from 'zod'
import { objectPick } from '@antfu/utils'

import { parseISO } from 'date-fns'
import type { ScheduledDeparture } from '@/shared/types'
import type { ScheduledDepartureSchema } from '../schemas'

import { normalizeTour } from '../tour'
import { normalizeDeparture } from '../departure'
import { normalizeTourBooking } from '../booking'

export function normalizeScheduledDeparture(
  scheduledDeparture: z.infer<typeof ScheduledDepartureSchema>,
): ScheduledDeparture {
  const { tour, departure, tourBookings, scheduledUsers, bookingSystemAvailability, start, end } = scheduledDeparture

  return {
    ...objectPick(
      scheduledDeparture,
      ['id', 'comment', 'totalPassengerCount', 'status'],
    ),
    tour: normalizeTour(tour),
    departure: normalizeDeparture(departure),
    tourBookings: (tourBookings ?? []).map(normalizeTourBooking),
    scheduledUsers: scheduledUsers.map(
      (u) => objectPick(u, ['userId', 'vehicleId']),
    ),
    bookingSystemAvailability,
    start: parseISO(start),
    end: parseISO(end),
  }
}
