import { z } from 'zod'
import { format } from 'date-fns'
import { objectPick } from '@antfu/utils'

import type { OperationalBase, ScheduledDeparture, ScheduledDepartureStatus, User, Vehicle } from '@/shared/types'
import type { RequireAtLeastOne } from '@/shared/lib/utils'

import { type ApiEndpointsAndSchemas, type ToKeyParams, client } from '../lib'

import { ScheduledDepartureSchema } from '../schemas'
import { normalizeScheduledDeparture } from './normalizers'

export const endpoints = {
  getByOperationalBaseAndDate: {
    url: (
      { operationalBaseId, date }: GetDeparturesByOperationalBaseAndDateParams,
    ) => `/v1/operational-bases/${operationalBaseId}/scheduled-departures/${date instanceof Date ? format(date, 'yyyy-MM-dd') : date}`,
    method: 'get',
    schema: z.object({ data: z.array(ScheduledDepartureSchema) }),
  },
  getById: {
    url: (
      { scheduledDepartureId }: GetDepartureByIdParams,
    ) => `/v1/scheduled-departures/${scheduledDepartureId}`,
    method: 'get',
    schema: z.object({ data: ScheduledDepartureSchema }),
  },
  update: {
    url: (
      { scheduledDepartureId }: Pick<
        UpdateScheduledDepartureParams,
        'scheduledDepartureId'
      >,
    ) => `/v1/scheduled-departures/${scheduledDepartureId}`,
    method: 'put',
    schema: z.object({ data: ScheduledDepartureSchema.pick({ comment: true, status: true }) }),
  },
  suggestResources: {
    url: (
      { scheduledDepartureId }: GetDepartureByIdParams,
    ) => `/v1/scheduled-departures/${scheduledDepartureId}/suggest`,
    method: 'get',
    schema: z.object({
      users: z.array(z.number()),
      vehicles: z.array(z.number()),
    }),
  },
  updateScheduledUsers: {
    url: (
      { scheduledDepartureId }: Pick<
        UpdateScheduledDepartureParams,
        'scheduledDepartureId'
      >,
    ) => `/v1/scheduled-departures/${scheduledDepartureId}/scheduled-users`,
    method: 'put',
    schema: z.object({ data: ScheduledDepartureSchema.shape.scheduledUsers }),
  },
} satisfies ApiEndpointsAndSchemas

export { endpoints as departureEndpoints }

export type GetDeparturesByOperationalBaseAndDateParams = {
  operationalBaseId: OperationalBase['id']
  date: Date
}
export type GetDeparturesByOperationalBaseAndDateKeyParams = ToKeyParams<
  GetDeparturesByOperationalBaseAndDateParams
>

export async function getDeparturesByOperationalBaseAndDate(
  { operationalBaseId, date }: GetDeparturesByOperationalBaseAndDateParams,
) {
  const { url, method, schema } = endpoints.getByOperationalBaseAndDate
  const { data } = await client[method](
    url({ operationalBaseId, date }),
    schema,
  )

  return data.map(normalizeScheduledDeparture)
}

export type GetDepartureByIdParams = { scheduledDepartureId: ScheduledDeparture['id'] }
export type GetDepartureByIdKeyParams = ToKeyParams<GetDepartureByIdParams>

export async function getDepartureById(
  { scheduledDepartureId }: GetDepartureByIdParams,
) {
  const { url, method, schema } = endpoints.getById

  const { data } = await client[method](url({ scheduledDepartureId }), schema)

  return normalizeScheduledDeparture(data)
}

export type UpdateScheduledDepartureParams = {
  scheduledDepartureId: ScheduledDeparture['id']
} & RequireAtLeastOne<{
  comment: string | null
  status: ScheduledDepartureStatus
}>
export async function updateScheduledDeparture(
  { scheduledDepartureId, comment, status }: UpdateScheduledDepartureParams,
) {
  const { url, method, schema } = endpoints.update

  const { data } = await client[method](
    url({ scheduledDepartureId }),
    {
      comment,
      status,
    },
    schema,
  )

  return objectPick(data, ['comment', 'status'])
}

export async function getDepartureSuggestedResources(
  { scheduledDepartureId }: GetDepartureByIdParams,
) {
  const { url, method, schema } = endpoints.suggestResources

  const { users, vehicles } = await client[method](
    url({ scheduledDepartureId }), schema,
  )

  return { users, vehicles }
}

export type UpdateScheduledDepartureUsersParams = {
  scheduledDepartureId: ScheduledDeparture['id']
  scheduledUsers: { userId: User['id']; vehicleId: Vehicle['id'] | null }[]
}
export async function updateScheduledDepartureUsers(
  { scheduledDepartureId, scheduledUsers }: UpdateScheduledDepartureUsersParams,
) {
  const { url, method, schema } = endpoints.updateScheduledUsers

  const { data } = await client[method](
    url({ scheduledDepartureId }),
    { scheduledUsers },
    schema,
  )

  return data.map((s) => objectPick(s, ['userId', 'vehicleId']))
}
